import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
import Loading from '../../components/loading/loading'
import {BaseUrl} from '../../common/js/util'
export default {
  name: "gift-paper",
  components:{Scroll,Loading},
  data(){
    return{
      id:"",
      pullup: true,
      dataList:[],
      token_id:"",
      activityData: [],
      homeActivityData:[],
      hasMore: true,
      page:1,
      code:""
    }
  },
  mounted(){

  },
  created(){
    this.$destroy();
    this.id=this.$route.params.id;
    this.token_id=localStorage.getItem("token_id");
    this.code = this.$store.state.code;
    this.getData(this.id,this.token_id)
  },

  methods:{
    searchMore() {
      if (this.homeActivityData.length >= 10) {
        this.$refs.loading.style.display = "flex"
        this.loading = true;
        this.token_id=localStorage.getItem("token_id");
        if(this.hasMore){
          this.page++;
          var url = BaseUrl+"customerLoginController.do?method=getActivitysByShiCode&page=" + this.page + "&rows=10&loginType=2&shiCode="+ this.code+"&token_id="+this.token_id
          console.log(url);
          axios.get(url).then((res) => {
            // this.activityData=res.data.dataList[0];
            console.log(res.data);
            if (res.data.ret === "-991" || res.data.ret === "-992" || res.data.ret === "-993") {
              alert("已在其他设备登录,请重新登录")
              this.$refs.loading.style.display = "none"
              this.$store.commit('updateFirst', false);
              this.$router.replace({
                path: '/login', query: {}
              });
              // this.$destroy();
            }
            else if(res.data.ret==="201"){
              alert("获取数据失败")
              setTimeout(function () {
                this.$router.go(-1);
                this.$destroy();
              },700)
            }
            else {
              if (res.data.dataList.length !== 0) {
                  this.$refs.loading.style.display = "none"
                  for(var i=0;i<res.data.dataList.length;i++){
                    this.homeActivityData.push(res.data.dataList[i]);
                  }
                  this.$store.commit('updateFirst', false);
                  console.log(this.homeActivityData)
              }
              else {
                console.log("没数据了")
                this.hasMore = false;
                this.$refs.loading.style.display = "none"
              }
            }
          })
          this.$refs.loading.style.display = "none"
          this.loading = false;
        }
        else {
          this.$refs.loading.style.display = "none"
          this.loading = false;
        }
      }
      else {
        this.$refs.loading.style.display = "none"
        this.loading = false;
      }
    },

    getDate(time) {
      const date = new Date(time);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
      const Y = date.getFullYear() + '-';
      const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
      const D = date.getDate() + ' ';
      const h = date.getHours() + ':';
      const m = date.getMinutes() + ':';
      const s = date.getSeconds();
      return Y + M + D + h + m + s;
    },

    getImage(item) {
      var imgId = item.coverImageId;
      return BaseUrl+"fileController.do?method=showimage&id=" + imgId + "&loginType=2&token_id="+this.token_id
    },

    getData(id,token_id){
      const url = BaseUrl+"customerLoginController.do?method=getActivitysByShiCode&page=1&rows=10&loginType=2&shiCode=" + this.code+"&token_id="+token_id;
      axios.get(url).then((res)=>{
        console.log(res.data)
        this.$refs.loading.style.display = "none"
        if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
          alert("已在其他设备登录,请重新登录")
          this.$router.replace({
            path: '/login',query:{}
          });
          this.$store.commit('updateIsLogin', false)
          this.$destroy();
        }else {
          this.$refs.loading.style.display = "none"
          this.homeActivityData=res.data.dataList
          console.log(this.homeActivityData)
        }
      })
    },
    selectItem(item){
      this.$router.push({
        path: `/shop/${item.merchantId}`
      })
    },
    //返回上一级页面
    back() {
      this.$router.go(-1);
      this.$destroy();
    },
  }
}



