import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
import Loading from '../../components/loading/loading'
import {BaseUrl} from '../../common/js/util'
export default {
  name: "gift-paper",
  components:{Scroll,Loading},
  data(){
    return{
      id:"",
      pullup: true,
      dataList:[],
      token_id:"",
      code:""
    }
  },
  created(){

  },

  activated(){
    if(!this.$route.meta.isUseCache) {
      this.id = this.$route.params.id;
      this.token_id = localStorage.getItem("token_id");
      this.code = this.$store.state.code;
      this.dataList=[];
      this.getData(this.id, this.token_id, this.code)
    }
    this.$route.meta.isUseCache = false;
  },



  methods:{
    // searchMore() {
    //   if (this.dataList.length > 5) {
    //     this.$refs.loading.style.display = "flex"
    //     this.loading = true;
    //     setTimeout(() => {
    //       let last = this.dataList[this.dataList.length - 1];
    //       for (let i = 1; i <= 10; i++) {
    //         this.dataList.push(last + i);
    //       }
    //       this.$refs.loading.style.display = "none"
    //       this.loading = false;
    //     }, 2500);
    //   }
    //   else {
    //     this.$refs.loading.style.display = "none"
    //     this.loading = false;
    //   }
    // },
    getData(id,token_id,code){
      axios.get(BaseUrl+"customerLoginController.do?method=getMerchantsByIndustryCategoryId&industryType="+id+"&loginType=2&token_id="+token_id+"&shiCode="+code).then((res)=>{
        console.log(res.data)
        this.$refs.loading.style.display = "none"
        if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
          alert("已在其他设备登录,请重新登录")
          this.$router.replace({
            path: '/login',query:{}
          });
          this.$store.commit('updateIsLogin', false)
          this.$destroy();
        }
        else if(res.data.ret==="201"){
          alert("获取数据失败")
          setTimeout(function () {
            this.$router.go(-1);
            this.$destroy();
          },1000)
        }
        else {
          this.dataList=res.data.dataList
        }
      })
    },
    selectItem(item){
      this.$router.push({
        path: `/shop/${item.merchantId}`
      })
    },
    getImg(item){
      if(this.dataList.length!==0){
        if(item.imageIds!=null&&item.imageIds!=="") {
          if(item.imageIds.indexOf(",")!==-1){
            var imgId = item.imageIds.split(",");
            return BaseUrl+"fileController.do?method=showimage&id=" + imgId[0] + "&loginType=2&token_id="+this.token_id
          }
          else {
            return BaseUrl+"fileController.do?method=showimage&id=" + item.imageIds + "&loginType=2&token_id="+this.token_id
          }
        }
        else {
          return ""
        }
      }
    },
    //返回上一级页面
    back() {
      this.$router.go(-1);
    },
  }
}



