router.beforeEach(function (to, from, next) {
  var nextRoute = ['home', 'me'];
  // 未登录状态；当路由到nextRoute指定页时，跳转至login
  if (nextRoute.indexOf(to.name) >= 0) {
    if (localStorage.getItem("login") !== "true") {
      console.log(localStorage.getItem("login"));
      router.replace({
        path: '/login', query: {}
      });
    }
  }
  next();
});

