
import $ from 'jquery'
import wx from 'weixin-js-sdk'
import comment from '../../components/public/comment'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import scroll from '../scroll/scroll'
import Loading from '../../components/loading/loading'
import axios from 'axios'
import {BaseUrl} from '../../common/js/util'
export default {
name: "shop_detail",
components:{comment,Swiper,SwiperItem,scroll,Loading},
data() {
return {
user_id:"",
curIdx: 0,
tabs: [],
dataList:[],
token_id:"",
imgUrls: [],
goods:[],
good: [],
indexImg: 'http://gw3.alicdn.com/bao/uploaded/i4/1798920415/TB21J8JpAKWBuNjy1zjXXcOypXa_!!1798920415-0-item_pic.jpg_210x210.jpg',
}
},
created(){

this.token_id=localStorage.getItem("token_id");
this.user_id=this.$route.params.id;
console.log( this.user_id)
this.getData(this.token_id);
},

mounted(){
this.InitWx();
},
methods: {
getImg(data){
this.imgUrls=data.imageIds.split(",");
},
getData(token_id){
axios.get(BaseUrl+"customerLoginController.do?method=getMerchantInfoById&merchantId="+this.user_id+"&loginType=2&token_id="+token_id).then((res)=>{
console.log(res.data.data)
this.$refs.loading.style.display = "none"
if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"||res.data.ret==="101"){
alert("已在其他设备登录,请重新登录")
this.$router.replace({
path: '/login',query:{}
});
this.$store.commit('updateIsLogin', false)
}
else if(res.data.ret==="201"){
alert("获取数据失败")
setTimeout(function () {
this.$router.go(-1);
this.$destroy();
},700)
}
else if(res.data.data===null){
setTimeout(()=>{
alert("暂无详情")
this.$router.go(-1);
this.$destroy();
},700)
}
else {
this.$refs.loading.style.display = "none"
this.dataList=res.data.data;
this.good=res.data.data.hotGoodsVos
this.getImg(res.data.data)
}
})
},
call(){
window.location.href = 'tel:'+this.dataList.telphone
},

InitWx(){
var that=this;
$.post('http://weixin20.shushangsoft.com/JsapiTicket/GetJsapiTicket/',
{ officialAccountId: 'gh_36d980f25e97', url: location.href.split('#')[0], jsApiLists: 'onMenuShareTimeline', debug: false }, function (config) {
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: config.appId,
timestamp: config.timestamp,
nonceStr: config.nonceStr,
signature: config.signature,
jsApiList: config.jsApiList
});
wx.ready(function () {
wx.onMenuShareTimeline({

title: "分享描述", // 分享标题

desc: "分享描述", //分享描述

link: that.$route.path, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
imgUrl: "http://gw3.alicdn.com/bao/uploaded/i4/1798920415/TB21J8JpAKWBuNjy1zjXXcOypXa_!!1798920415-0-item_pic.jpg_210x210.jpg", // 分享图标

});

})

})
},

selectItem(){
this.$router.push({
path: `/picture`
})
},


selectItemHot(item) {
this.$router.push({
path: `/hotgoodsdetail2/${item.hotgoodsId}`
})
},


getImage(item) {
var imgId = item.hotgoodsImages;
return BaseUrl+"fileController.do?method=showimage&id=" + imgId + "&loginType=2&token_id="+this.token_id
},


back() {


this.$router.back()
},

getSwiper(item){
return  BaseUrl+"fileController.do?method=showimage&id="+item+"&loginType=2&token_id="+this.token_id
}
},

beforeRouteLeave (to, from, next) {
if (to.name === 'activityshoplist'||to.name==="categoryshoplist"||to.name==="hotgoodsdetail") {
to.meta.isUseCache =true;
console.log("888")
}
next();
},

}



