
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import axios from 'axios'
import {BaseUrl} from '../../common/js/util'
export default {
name: "gift-paper",
data(){
return{
scanImg:false,
qrcodeImgUrl:"",
allPaper:[],
activityId:"",
token_id:"",
unUsePaper:[
"https://s1.ax1x.com/2018/07/28/PUdXy8.png",
],
paperIndex:0
}
},
created(){
this.token_id=localStorage.getItem("token_id");
this.getData( this.token_id)
},

methods:{
showCode(item){
this.$router.push({
path: `/game`
})
},
getData(token_id){
axios.get(BaseUrl+"customerLoginController.do?method=getMyGameCoupon&page=1&rows=5&loginType=2&token_id="+token_id).then((res)=>{
console.log(res)
if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("已在其他设备登录,请重新登录")
this.$router.replace({
path: '/login',query:{}
});
this.$destroy()
this.$store.commit('updateIsLogin', false)
}else {
if(res.data.dataList.length!==0){
this.allPaper=res.data.dataList;
this.activeId=res.data.dataList[0].activityId;
this.$store.commit('updateActivityId',  this.activeId)
}
else {
setTimeout(function () {
alert("您没有参加游戏")
},800)
}
}
})
},

back() {
this.$router.go(-1);
this.$destroy();
},
}
}



