
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
export default {
name: "gift-paper",
components:{Scroll},
data(){
return{
scanImg:false,
qrcodeImgUrl:"",
allPaper:[],
activityId:"",
iconMap: [],
unUsePaper:[
"https://s1.ax1x.com/2018/07/28/PUdXy8.png",
],
paperIndex:0
}
},
created(){
this.getData()
},

methods:{
showCode(item){
this.$router.push({
path: `/game`
})
},
getData(){
axios.get("http://e.shushangsoft.com/industryCategoryController.do?method=getIndustryCategorys&loginType=2").then((res) => {
console.log(res.data);
this.iconMap = res.data.dataList;
})
},

returnImg(item) {
switch (item.industryCategoryName) {
case "地板墙砖":
return require("../../common/image/diban.png");
break;
case "卫浴浴洁具":
return require("../../common/image/weiyu.png");
break;
case "厨房设施":
return require("../../common/image/chufang.png");
break;
case "家具软装":
return require("../../common/image/jiaju.png");
break;
case "五金水暖":
return require("../../common/image/shuinuan.png");
break;
case "灯饰开关":
return require("../../common/image/dengju.png");
break;
case "油漆涂装":
return require("../../common/image/tuzhuang.png");
break;
case "其它":
return require("../../common/image/all.png");
break;
case "门窗":
return require("../../common/image/chuanghu.png");
break;
}
},

selectCgtegory(item) {
this.$router.push({
path: `/categoryshoplist/${item.industryCategoryId}`
})
},

back() {
this.$router.go(-1);
this.$destroy();
},
}
}



