
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios'
import $ from 'jquery'
import {BaseUrl} from '../../common/js/util'
export default {
name: "login",
components:{XInput,Group,XButton},
data(){
return{
value2:'',
value1:'',
countdown:60
}
},

methods:{
getCode(){
if(this.value1===""){
alert("手机号不能为空")
}
else {
this.settime();
axios.get(BaseUrl+"customerLoginController.do?method=sendYzm&type=1&loginType=2&phone="+this.value1).then((res)=>{
})
}
},

settime(){

var o=$('.buttonCode');
if (this.countdown === 0) {
o.attr("disabled",false);
o.text("获取验证码");
this.countdown = 60;
} else { // www.jbxue.com
o.attr("disabled",true);
o.text(this.countdown+"秒后重发")
this.countdown--;
setTimeout(() => {
this.settime();
console.log(this.countdown)
},
1000)
}
},

login(){
axios.get(BaseUrl+"customerLoginController.do?method=login&phone="+this.value1+"&type=1&code="+this.value2+"&loginType=2").then((res)=>{

if(res.data.ret==="200"){
this.$router.replace({
path: '/home',query:{}
});
this.countdown===0;
this.$store.commit('updateIsLogin', true)
localStorage.setItem("login","true");
localStorage.setItem("first_login","true");
localStorage.setItem("reLogin","false");
localStorage.setItem("token_id",res.data.data.token_id);
this.$destroy();
}else {
alert(res.data.msg)
}
})
}
}
}



