getLocation(token_id) {
var geolocation = new BMap.Geolocation()
geolocation.getCurrentPosition((r) => {
if (r.point) {

var myGeo = new BMap.Geocoder();

myGeo.getLocation(new BMap.Point(r.longitude, r.latitude), (res) => {
if (res) {
this.firstCity = res.addressComponents.city;
a:for (var i = 0; i < this.cityData.length; i++) {
b:for (var j = 0; j < this.cityData[i].list.length; j++) {
if (res.addressComponents.city=== this.cityData[i].list[j].name) {
this.firstCity = res.addressComponents.city;

this.firstCode = this.cityData[i].list[j].zip
this.$store.commit('updateCode', this.firstCode);
this.$store.commit('updateCity',this.firstCity);
console.log("定位成功")
console.log("定位城市:"+this.firstCity)
break a;
}
else {
console.log("定位失败")
this.firstCity = "上海市";
this.firstCode = "310100"
this.$store.commit('updateCode', "310100");
}
}
}

this.getActivityData(this.firstCode,token_id);
}
});
}
