
import $ from 'jquery'
import wx from 'weixin-js-sdk'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import scroll from '../scroll/scroll'
import Loading from '../../components/loading/loading'
import axios from 'axios'
import {BaseUrl} from '../../common/js/util'
export default {
name: "hot-goos-detail",
components:{Swiper,SwiperItem,scroll,Loading},
data(){
return{
id:"",
goodDetail:{},
imgUrls: [],
}
},
created(){
},

activated(){
if(!this.$route.meta.isUseCache) {
this.id = this.$route.params.id;
this.token_id = localStorage.getItem("token_id");
this.goodDetail={};
this. imgUrls=[]
this.getData(this.id, this.token_id);
}
this.$route.meta.isUseCache = false;
},

methods:{

getData(id,token_id){
const url = BaseUrl+"customerLoginController.do?method=getHotGoodsByHotGoodsId&hotGoodsId="+id+"&token_id="+token_id;;
axios.get(url).then((res)=>{
console.log(res.data)
if(res.data.ret==="101"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("已在其他设备登录,请重新登录")
this.$router.replace({
path: '/login',query:{}
});
this.$store.commit('updateIsLogin', false)
}
else if(res.data.ret==="201"){
alert("获取数据失败")
this.$refs.loading.style.display = "none"
setTimeout(function () {
this.$router.go(-1);
this.$destroy();
},700)
}
else {
this.$refs.loading.style.display = "none"
this.goodDetail=res.data.data;
this.getImg(res.data.data)
var img=document.getElementsByClassName("swiper-img")
}
})
},


getImg(data){
this.imgUrls=data.browseImages.split(",");
},

gotoShop(){
this.$router.push({
path: `/shop/${this.goodDetail.merchantId}`
})
},

getSwiper(item){
return  BaseUrl+"fileController.do?method=showimage&id="+item+"&loginType=2&token_id="+this.token_id
},


getHotGoodsImg(){
return  BaseUrl+"fileController.do?method=showimage&id="+this.goodDetail.hotgoodsImages+"&loginType=2&token_id="+this.token_id
},

shareToweixin(){
alert("目前无法分享");
},


back() {
this.$router.go(-1);
},
}
}



