import SearchBox from '../../components/search-box/search-box'
import Scroll from "../../components/scroll/scroll"
import axios from 'axios'
import {BaseUrl} from '../../common/js/util'
  export default {
      name: "search",
    components:{SearchBox,Scroll},
    data(){
        return{
          query: '',
          dataList:'',
          token_id:"",
        }
    },
    created(){
      this.token_id=localStorage.getItem("token_id");
    },
    methods:{
      //搜索发生变化，得到搜索的文字
      onQueryChange(query) {
        this.query = query
      },
      selectItem(item){
        this.$router.push({
          path: `/shop/${item.merchantId}`
        })
      },
      //返回上一级页面
      back() {
        this.$router.go(-1);
        this.$destroy();
      },
      getImg(item){
        if(this.dataList.length!==0){
          if(item.imageIds!=null&&item.imageIds!=="") {
            if(item.imageIds.indexOf(",")!==-1){
              var imgId=item.imageIds.split(",");
              return BaseUrl+"fileController.do?method=showimage&id="+imgId[0]+"&loginType=2&token_id="+this.token_id
            }
            else {
              return BaseUrl+"fileController.do?method=showimage&id=" + item.imageIds + "&loginType=2&token_id="+this.token_id
            }
          }
          else {
            return ""
          }
        }
      },
    },
    watch:{
      query(newQuery){
        if(newQuery!==""){
          // 请求网络
          axios.get(BaseUrl+"customerLoginController.do?method=getMerchantsByIndustryCategoryId&merchantName="+newQuery+"&loginType=2&token_id="+this.token_id).then((res)=>{
            this.dataList=res.data.dataList
          })
        }
      }
    },
  }



