
import Tab from '../../components/public/navbar'
import axios from 'axios'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {BaseUrl} from '../../common/js/util'
import { MessageBox } from 'mint-ui';
import $ from 'jquery'
import wx from 'weixin-js-sdk'
export default {
name: "me",
directives: {
TransferDom
},
components:{Tab,XButton,Alert,TransferDom},
data(){
return{
openSimple: false,
show:false,
img_url:"",
qrcode_url:"",
user_name:"",
token_id:"",
c:"",
isFirst:true
}
},
created(){
console.log("回来了1")
this.token_id=localStorage.getItem("token_id");
this.qrcode_url=BaseUrl+"customerLoginController.do?method=getBarCode&type=2&loginType=2&token_id="+this.token_id
if(this.isFirst){
this.getData();
this.getCode();
}
},

activated(){
console.log("回来了2")
},
methods:{

openSimpleDialog () {
this.openSimple = true;
},
closeSimpleDialog () {
this.openSimple = false;
},
getData(){
axios.get(BaseUrl+"customerLoginController.do?method=getCustomerInfo&loginType=2&token_id="+this.token_id).then((res)=>{
if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("已在其他设备登录,请重新登录")
this.$store.commit('updateFirst', false);
this.$router.replace({
path: '/login',query:{}
});
this.$store.commit('updateIsLogin', false)
this.$destroy();
}else {
console.log(res.data.data)
this.isFirst=false;
this.user_name=res.data.data.customerName;
this.$store.commit("updateIsLogin",false);
this.$store.commit('updateFirst', false);
}
})
},

getCode(){
axios.get(BaseUrl+"customerLoginController.do?method=getBarCode&type=2&loginType=2&token_id="+this.token_id).then((res)=>{
this.img_url=res
})
},
refreshImg(){
this.$refs.qrcode.src=BaseUrl+"customerLoginController.do?method=getBarCode&type=2&loginType=2"+"&_r="+Math.random()+"&token_id="+this.token_id
console.log("http://e.shushangsoft.com/customerLoginController.do?method=getBarCode&type=2&loginType=2"+"&_r="+Math.random())

},
mypaper(){
this.$router.push({
path: `/gitpaper`
})
},
myGame(){
this.$router.push({
path: `/gamelist`
})
},

quit(){

this.$messagebox({

title: '提示',

message: '确定退出吗',

showCancelButton: true,

confirmButtonText:"确定",

cancelButtonText:"取消"

}).then(action => {
if(action == 'confirm'){
localStorage.setItem("token_id",null);
localStorage.setItem("quit","true");
localStorage.setItem("refresh","true");
this.$store.commit('updateFirst', false);
this.$router.replace({
path: '/login', query: {}
});
}else{

}
})

},
exchange(){
this.$router.push({
path: `/exchangepaper`
})
}
},
beforeDestroy(){
this.$store.commit("updateIsLogin",true);
},
}



