
import $ from 'jquery'
import wx from 'weixin-js-sdk'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import axios from 'axios'
import { MessageBox } from 'mint-ui';
import {BaseUrl} from '../../common/js/util'
export default {
name: "game",
components:{Alert,TransferDom},
data() {
return {
scanImg:false,
show:false,
flag: false,
Logo:[],
ColorLogo:[],
gameId:"",
merchants:[],
code_num:[],
url3:"",
token_id:"",
activityId:"",
}
},
created() {
var that=this;
this.token_id=localStorage.getItem("token_id");
this.activityId=this.$store.state.activityId
axios.get(BaseUrl+"activityGameController.do?method=getGames&activityId="+this.activityId+"&loginType=2&token_id="+this.token_id).then((res)=>{
if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("已在其他设备登录,请重新登录")
this.$router.replace({
path: '/login',query:{}
});
this.$destroy();
this.$store.commit('updateIsLogin', false);
}
else{
if(res.data.data==null){
setTimeout(function () {
alert("目前没有游戏")
that.$router.go(-1);
that.$destroy()
},1000)
}
else {
this.gameId=res.data.data.gameId
this.merchants=res.data.data.merchants;
axios.get(BaseUrl+"activityGameController.do?method=getMerchantsLogo&loginType=2&gameId="+this.gameId+"&token_id="+this.token_id).then((res)=>{
console.log(res.data.dataList)
that.getImage(res.data.dataList)

that.url3=BaseUrl+"activityGameController.do?method=getCustomerGameInfo&gameId="+this.gameId+"&loginType=2&token_id="+this.token_id
axios.get(that.url3).then((res)=>{
if(res.data.dataList.length!==0){
if(res.data.dataList[0].paixu===5){
setTimeout(function () {
alert("您已经通关此游戏了")
that.$router.go(-1);
that.$destroy()
},800)
}
else {
that.$messagebox({

title: '提示',

message: '从上往下点击商标图片进行游戏',

showCancelButton: true,

confirmButtonText:"确定",

cancelButtonText:"取消"

}).then(action => {

if(action == 'confirm'){
that.play();
}else{
this.$router.go(-1);
this.$destroy()
}
})
this.setGameState(res.data);
}
}
else {
that.$messagebox({

title: '提示',

message: '从上往下点击商标图片进行游戏',

showCancelButton: true,

confirmButtonText:"确定",

cancelButtonText:"取消"

}).then(action => {

if(action == 'confirm'){
that.play();
}else{
this.$router.go(-1);
this.$destroy()
}
})
}
})
})
}
}
})
console.log(location.href.split('#')[0]);
},
mounted() {
this.audioAutoPlay('music')

this.InitWx();
},
methods: {
audioAutoPlay(id){
var audio = document.getElementById(id);
audio.play();
document.addEventListener("WeixinJSBridgeReady", function () {
audio.play();
}, false);
},
gotoPaper(){
this.$router.replace({
path: `/exchangepaper`
})
this.$destroy();
},
showOrhide(){
this.scanImg=!this.scanImg;
},
setGameState(data){
if(data.dataList.length!==0){
const paixu=data.dataList[0].paixu;
const boat=$('.boat')
if(paixu===1){
boat.css({"bottom":" 68%","left":"52%"})
$('.brand-position0').children("img").removeClass("gray")
$('.brand-position0').children("img").attr("disabled",false)
}else if(paixu===2){
boat.css({"bottom":" 55%","left":"29%"})
$('.brand-position1').children("img").removeClass("gray")
$('.brand-position0').children("img").removeClass("gray")
$('.brand-position0').children("img").attr("disabled",false)
$('.brand-position1').children("img").attr("disabled",false)
}else if(paixu===3){
boat.css({"bottom":" 38%","left":"32%"})
$('.brand-position3').children("img").removeClass("gray")
$('.brand-position1').children("img").removeClass("gray")
$('.brand-position0').children("img").removeClass("gray")
}else if(paixu===4){
boat.css({"bottom":" 15%","left":"70%"})
$('.brand-position4').children("img").removeClass("gray")
$('.brand-position3').children("img").removeClass("gray")
$('.brand-position1').children("img").removeClass("gray")
$('.brand-position0').children("img").removeClass("gray")
}
else if(paixu===5){
boat.css({"bottom":" 10%","left":"50%"})
$('.brand-position6').children("img").removeClass("gray")
$('.brand-position4').children("img").removeClass("gray")
$('.brand-position3').children("img").removeClass("gray")
$('.brand-position1').children("img").removeClass("gray")
$('.brand-position0').children("img").removeClass("gray")
}
}

},
getImage(data){
for(let i=0;i<data.length;i++){
this.Logo.push(data[i].merchantColorLogo+"&loginType=2")
this.ColorLogo.push(data[i].merchantColorLogo+"&loginType=2")
}
},





InitWx(){
$.post('http://weixin20.shushangsoft.com/JsapiTicket/GetJsapiTicket/',
{ officialAccountId: 'gh_36d980f25e97', url: location.href.split('#')[0], jsApiLists: 'scanQRCode', debug: false }, function (config) {
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: config.appId,
timestamp: config.timestamp,
nonceStr: config.nonceStr,
signature: config.signature,
jsApiList: config.jsApiList
});
wx.ready(function() {
document.getElementById('music').play();
});

})

















wx.error(function (res) {
alert("出错了：" + res.errMsg);
});
},
clickCode(index) {
var that =this;
wx.ready(function () {
wx.checkJsApi({
jsApiList: ['scanQRCode'],
success: function (res) {
}
});
wx.scanQRCode({
needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
success: function (res) {
var result = res.resultStr; 
var boat=$('.boat')
if(index===4){
axios.get(BaseUrl+"activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+result+"&loginType=2&token_id="+that.token_id).then((res)=>{
if(res.data.ret==="200"){
axios.get(that.url3).then((res)=>{
if(res.data.dataList[0].paixu===5){
$('.brand-position4').children("img").removeClass("gray")
that.showOrhide();
boat.css({"bottom":" 10%","left":"50%"})
that.$store.commit("updatePaixu",res.data.dataList.paixu);
}
else {
alert("请按顺序进行")
}
})
}
else {
alert(res.data.msg)
}
})
}
else if(index===3){
axios.get(BaseUrl+"activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+result+"&loginType=2&token_id="+that.token_id).then((res)=>{
if(res.data.ret==="200"){
axios.get(that.url3).then((res)=>{
if(res.data.dataList[0].paixu===4){
$('.brand-position3').children("img").removeClass("gray")
boat.css({"bottom":" 15%","left":"70%"})
}
else {
alert("请按顺序进行")
}
})
}
else {
alert(res.data.msg)
}
})
}
else if(index===2){
axios.get(BaseUrl+"activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+result+"&loginType=2&token_id="+that.token_id).then((res)=>{
if(res.data.ret==="200"){
axios.get(that.url3).then((res)=>{
if(res.data.dataList[0].paixu===3){
$('.brand-position2').children("img").removeClass("gray")
boat.css({"bottom":" 38%","left":"32%"})
}
else {
alert("请按顺序进行")
}
})
}
else {
alert(res.data.msg)
}
})
}
else if(index===1){
axios.get(BaseUrl+"activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+result+"&loginType=2&token_id="+that.token_id).then((res)=>{
if(res.data.ret==="200"){
axios.get(that.url3).then((res)=>{
if(res.data.dataList[0].paixu===2){
$('.brand-position1').children("img").removeClass("gray")
boat.css({"bottom":" 55%","left":"29%"})
}else {
alert("请按顺序进行")
}
})
}
else {
alert(res.data.msg)
}
})
}
else if(index===0){
axios.get(BaseUrl+"activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+result+"&loginType=2&token_id="+that.token_id).then((res)=>{
if(res.data.ret==="200"){
axios.get(that.url3).then((res)=>{
if(res.data.dataList[0].paixu===1){
$('.brand-position0').children("img").removeClass("gray")
boat.css({"bottom":" 68%","left":"52%"})
}
else {
alert("请按顺序进行")
}
})
}
else {
alert(res.data.msg)
}
}).reject((error)=>{

})
}
}
});

});














































},
play() {
this.flag = true;
const audio = document.getElementById('music');
audio.play();
},

back() {
this.$router.go(-1);
this.$destroy()
},
setClass(index) {
if (index === 0) {
return "brand-position0"
}
else if (index === 1) {
return "brand-position1"
}
else if (index === 2) {
return "brand-position3"
}
else if (index === 3) {
return "brand-position4"
}
else if (index === 4) {
return "brand-position6"
}
}
}
}



