import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
import Loading from '../../components/loading/loading'
import {BaseUrl} from '../../common/js/util'
export default {
  name: "gift-paper",
  components:{Scroll,Loading},
  data(){
    return{
      id:"",
      pullup: true,
      dataList:[],
      token_id:"",
      hasMore: true,
      page:1
    }
  },
  created(){
    // this.id=this.$route.params.id;
    // this.token_id=localStorage.getItem("token_id");
    // this.getData(this.id,this.token_id)
    console.log("666")
  },

  activated() {
    console.log("666")
    // isUseCache为false时才重新刷新获取数据
    // 因为对list使用keep-alive来缓存组件，所以默认是会使用缓存数据的
    if(!this.$route.meta.isUseCache){
      console.log("777")
      this.id=this.$route.params.id;
      this.token_id=localStorage.getItem("token_id");
      this.dataList = []; // 清空原有数据
      this.getData(this.id,this.token_id); // 这是我们获取数据的函数
    }
    this.$route.meta.isUseCache = false;
  },
  methods:{
    searchMore() {
      if (this.dataList.length >=10) {
        this.$refs.loading.style.display = "flex"
        if(this.hasMore){
          this.page++;
          axios.get(BaseUrl+"customerLoginController.do?method=getMerchantsByAcId&activityId="+ this.id+"&page="+this.page+"&rows=10&loginType=2&token_id="+this.token_id).then((res)=>{
            if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
              alert("已在其他设备登录,请重新登录")
              this.$router.replace({
                path: '/login',query:{}
              });
              this.$destroy();
            }
            else if(res.data.ret==="201"){
              alert("获取数据失败")
              setTimeout(function () {
                this.$router.go(-1);
                this.$destroy();
              },1000)
            }
            else {
              console.log("wolaile")
              if(res.data.dataList.length!==0){
                this.$refs.loading.style.display = "none"
                for(var i=0;i<res.data.dataList.length;i++){
                  this.dataList.push(res.data.dataList[i])
                }
              }
              else {
                console.log("没数据了")
                this.hasMore = false;
                this.$refs.loading.style.display = "none"
              }
            }
          })
        }
        else {
          this.$refs.loading.style.display = "none"
          this.loading = false;
        }
      }
      else {
        this.$refs.loading.style.display = "none"
        this.loading = false;
      }
    },
    getImg(item){
      if(this.dataList.length!==0){
        if(item.imageIds!=null&&item.imageIds!=="") {
          if(item.imageIds.indexOf(",")!==-1){
            var imgId = item.imageIds.split(",");
            return BaseUrl+"fileController.do?method=showimage&id=" + imgId[0] + "&loginType=2&token_id="+this.token_id
          }
          else {
            return BaseUrl+"fileController.do?method=showimage&id=" + item.imageIds + "&loginType=2&token_id="+this.token_id
          }
        }
        else {
          return ""
        }
      }
    },

    getData(id,token_id){
      axios.get(BaseUrl+"customerLoginController.do?method=getMerchantsByAcId&activityId="+id+"&page=1&rows=10&loginType=2&token_id="+token_id).then((res)=>{
        console.log(res.data)
        this.$refs.loading.style.display = "none"
        if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
          alert("已在其他设备登录,请重新登录")
          this.$router.replace({
            path: '/login',query:{}
          });
          this.$store.commit('updateIsLogin', false)
          this.$destroy();
        }
        else if(res.data.ret==="201"){
          alert("获取数据失败")
          setTimeout(function () {
            this.$router.go(-1);
            this.$destroy();
          },700)
        }
        else {
          this.$refs.loading.style.display = "none"
          this.dataList=res.data.dataList
        }
      })
    },
    selectItem(item){
      this.$router.push({
        path: `/shop/${item.merchantId}`
      })
    },
    //返回上一级页面
    back() {
      // this.$router.go(-1);
      // this.$destroy();
      this.$router.back()
    },
  },

  destroyed(){
    console.log("销毁了")
  }
}



