
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import axios from 'axios'
import {BaseUrl} from '../../common/js/util'
export default {
name: "gift-paper",
data(){
return{
loading: false,
scanImg:false,
qrcodeImgUrl:"",
Paper:[],
page:1,
token_id:"",
allPaper:[],
unUsePaper:[
],
usedPaper:[

],
active: 0,
paperIndex:0
}
},
components:{ButtonTab, ButtonTabItem},

created(){
this.token_id=localStorage.getItem("token_id");
this.getData(this.token_id)
},

methods:{
showCode(item){
this.scanImg=true;
this.qrcodeImgUrl=BaseUrl+"customerLoginController.do?method=getCouponBarCode&type=2&couponId="+item.couponId+"&loginType=2&token_id="+this.token_id
},
hideIma(){
this.scanImg=false;
this.qrcodeImgUrl="";
},
getData(token_id){
axios.get(BaseUrl+"customerLoginController.do?method=getMyCoupon&page=1&rows=10&loginType=2&token_id="+token_id).then((res)=>{
console.log(res.data)

if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("已在其他设备登录,请重新登录")
this.$router.replace({
path: '/login',query:{}
});
}
else {
this.Paper=res.data.dataList;
this.allPaper=res.data.dataList
for(var i=0;i<this.Paper.length;i++){
if(this.Paper[i].statues==="2"){
this.usedPaper.push(this.Paper[i])
}
else if(this.Paper[i].statues==="0"||this.Paper[i].statues===null){
this.unUsePaper.push(this.Paper[i])
}
}
}
})
},
loadMore() {
this.$refs.loading.style.display = "flex"
this.loading = true;
if(this.active===0){
if (this.allPaper.length > 10) {
this.page++;
axios.get(BaseUrl+"customerLoginController.do?method=getMyCoupon&page="+this.page+"&rows=10&loginType=2&token_id="+this.token_id).then((res)=>{
console.log(res.data)
if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("已在其他设备登录,请重新登录")
this.$router.replace({
path: '/login',query:{}
});
}
else if(res.data.ret==="201"){
alert("获取数据失败")
setTimeout(function () {
this.$router.go(-1);
this.$destroy();
},700)
}
else if(res.data.dataList.length!==0) {
for(var i=0;i<res.data.dataList.length;i++){
this.allPaper.push(res.data.dataList[i]);
}
}
else {
console.log("没有数据了")

this.loading = false;
}
})
}
else {

this.loading = false;
}
}
else if(this.active===1||this.active===2){
if (this.unUsePaper.length > 10||this.usedPaper.length>10) {
this.page++;
axios.get(BaseUrl+"customerLoginController.do?method=getMyCoupon&page="+this.page+"&rows=10&loginType=2&token_id="+this.token_id).then((res)=>{
console.log(res.data)
if(res.data.ret==="-991"||res.data.ret==="-992"||res.data.ret==="-993"){
alert("已在其他设备登录,请重新登录")
this.$router.replace({
path: '/login',query:{}
});
}
else if(res.data.ret==="201"){
alert("获取数据失败")
setTimeout(function () {
this.$router.go(-1);
this.$destroy();
},700)
}
else if(res.data.dataList.length!==0) {
for(var i=0;i<res.data.dataList.length;i++) {
if(res.data.dataList[i].statues==="2"){
this.usedPaper.push(res.data.dataList[i])
}
else if(res.data.dataList[i].statues==="0"||res.data.dataList[i].statues===null){
this.unUsePaper.push(res.data.dataList[i])
}
}
}
else {
console.log("没有数据了")

this.loading = false;
}
})
}
else {

this.loading = false;
}
}
},
siwtchpaper(index){
this.index=index
},

back() {
this.$router.go(-1);
this.$destroy();
},
}
}



